import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { casesPage } from "../../../../constants";
import cn from "classnames";
import styles from "./hero.module.css";

const Hero = () => {
  const [cases, setCases] = useState([]);
  const [amount, setAmount] = useState(0);
  const [casesShow, setCasesShow] = useState([]);
  const [btnShow, setBtnShow] = useState(true);
  const [width, setWidth] = useState(0);

  useEffect(() => {
    function handleResize() {
      setWidth(window.innerWidth);
    }

    window.addEventListener("resize", handleResize);

    handleResize();

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  });

  useEffect(() => {
    setCases([...casesPage]);
  }, []);

  useEffect(() => {
    if (width < 1440) {
      setAmount(6);
    } else if (width >= 1440) {
      setAmount(9);
    }
  }, [width]);

  useEffect(() => {
    setCasesShow(cases.slice(0, amount));
  }, [amount, cases]);

  useEffect(() => {
    if (cases.length === casesShow.length) {
      setBtnShow(false);
    } else {
      setBtnShow(true);
    }
  }, [cases.length, casesShow.length]);

  const handleClick = () => {
    setAmount(amount + 3);
  };

  return (
    <section className={styles.hero}>
      <div className={styles.hero__container}>
        <h2 className={styles.hero__title}>Наши кейсы</h2>
        <div className={styles.hero__wrapper}>
          <ul className={styles.hero__list}>
            {casesShow?.map(({ id, path, heading, imgBlue, imgGray, alt }) => {
              return (
                <li className={styles.hero__item} key={id}>
                  <Link className={styles.hero__link} to={`/cases/${path}`}>
                    <div className={styles.hero__info}>
                      <h4 className={styles.hero__heading}>
                        {heading} <span className={styles.hero__arrow}>→</span>
                      </h4>
                    </div>
                    <img
                      src={imgGray}
                      alt={alt}
                      className={cn(
                        styles.hero__img,
                        styles["hero__img--gray"]
                      )}
                    />
                    <img
                      src={imgBlue}
                      alt={alt}
                      className={cn(
                        styles.hero__img,
                        styles["hero__img--blue"]
                      )}
                    />
                  </Link>
                </li>
              );
            })}
          </ul>
          <button
            type="button"
            className={cn(styles.hero__btn, {
              [styles["hidden"]]: btnShow === false,
            })}
            onClick={handleClick}
          >
            Показать ещё
          </button>
        </div>
      </div>
    </section>
  );
};

export default Hero;
